.header {
  width: 350px;
  padding: 20px;
  &:hover {
    background-color: rgba(150, 225, 250, 0.829);
  }
  & img{
    padding: 0 15px 0 15px
  }
 & svg{
    font-size: 27px;
    margin-right: 20px;
    cursor: pointer;
    overflow: visible;
    width: 0.875em;
    display: inline-block;
    height: 1em;
    vertical-align: -0.125em;
 }
}