.join {
  position: fixed;
  width: 100%;
  z-index: 10;
  bottom: 0;
  right: 0;
  height: 80px;
  background-color: #232323;
  & .actual-cta-btn {
    display: flex;
    width: 300px;
    border-radius: 28px;
    background-color: #2fcf0c;
    color: #fff;
    font-size: 20px;
    font-weight: 400;
    text-decoration: none;
    padding: 10px 30px;
    margin: auto;
  }
}
