.questions {
  padding-top: 73px;
  margin: 0 -50px;
  & .wrapper {
    max-width: 1024px;
    margin: auto;
  }
  & h3 {
    text-align: center;
    background-color: #fff;
    padding: 20px 0;
    margin: 0;
    font-size: 27px;
    font-weight: bold;
    color: #121212;
    border-top: solid 2px #26cc00;
    border-bottom: solid 2px #26cc00;
  }

  & li {
    position: relative;
    padding: 20px 0;
    padding-left: 50px;
    border-bottom: solid 1px #eee;
    color: #121212;
    font-size: 15px;
    line-height: 1.5;
    cursor: pointer;
    font-weight: bold;
    & :hover {
      color: #26cc00;
    }
  }
  & img {
    height: 18px;
    align-self: center;
  }
}
