.aditional-products {
  text-align: center;
  padding: 70px 0 70px 0;

  & h3 {
    display: block;
    font-size: 18px;
    text-align: center;
    font-weight: bold;
    color: #121212;
    margin: 0;
    line-height: 1.2;
  }
  & .cards {
    display: flex;
    justify-content: space-around;
    margin-top: 40px;
    margin: 40px 50px 0 50px;
    & .card {
      position: relative;
      width: 100%;
      padding: 30px 0;
      background-color: #fff;
      border-top: 0px;
      text-align: center;
      box-shadow: 0 0 10px rgb(0 0 0 / 10%);
      transform: none;
      border-radius: 10px;
      flex: 0 0 31%;
      & .p-logo {
        display: block;
        max-width: 200px;
        max-height: 40px;
        width: auto;
        height: auto;
      }
      & .picture {
        margin-top: 30px;
        max-height: 160px;
      }
      & p {
        padding: 0;
        font-size: 15px;
        color: #282828;
        margin: 20px auto;
        font-weight: 300;
        line-height: 1.2;
        max-width: 285px;
      }
      & .btn {
        position: relative;
        display: inline-block;
        padding: 0;
        line-height: 34px;
        width: 191px;
        border-radius: 20px;
        background-color: #3c3c3c;
        color: #fff;
        font-size: 20px;
        font-weight: 300;
        text-decoration: none !important;
        z-index: 7;
        -webkit-transition: all 0.15s ease;
        -moz-transition: all 0.15s ease;
        -o-transition: all 0.15s ease;
        transition: all 0.15s ease;
        &:hover {
          background-color: #26cc00;
        }
      }
    }
  }
}
