.back {
  font-size: 20px;
  font-weight: 100;
  color: #282828;
  padding: 0px 20px 14px;
  text-decoration: none;
  & :hover {
    cursor: pointer;
  }
}

.main {
  padding: 50px 60px 50px;
  background-color: #fff;
  border-radius: 10px 10px 0 0;
  box-shadow: 0 0 10px rgb(0 0 0 / 10%);
  padding: 60px 50px 0px;

  & .main-right-section {
    flex: 0 0 66.666667%;
    max-width: 66.666667%;

    & .trade-pic {
      vertical-align: middle;
      border-style: none;
      border: none 0;
      max-width: 280px;
    }
    & h3 {
      margin: 0;
      font-size: 40px;
      font-weight: 400;
      color: #282828;
      margin-bottom: 40px;
      text-align: right;
      font-weight: bold;
    }
    & li {
      font-weight: bold;
      & img {
        height: 55px;
        width: 55px;
      }
      & span {
        background-color: #26cc00;
        width: 100px;
        height: 5px;
        margin: 20px 0;
        display: inline-block;
      }
    }
    & p {
      padding: 25px 0 50px;
      font-size: 18px;
      font-weight: 300;
      color: #282828;
      text-align: right;
      max-width: 600px;
      margin: 0;
    }
  }
  & .main-left-section {
    & .main-pic {
      margin-bottom: 60px;
    }

    & .video-section {
      & img {
        position: absolute;
        font-size: 60px;
        color: #fff;
        text-shadow: 0 0 20px rgb(0 0 0 / 30%);
        margin: auto;
      }

      & .video {
        position: relative;
        margin: auto;
        margin-top: 0;
        width: 100%;
        max-width: 100%;
        border: none 0;
        height: 170px;
        cursor: pointer;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        border-radius: 10px;
      }
      & .minisite-button {
        width: 100%;
        margin-top: 25px;
        font-size: 22px;
        font-weight: 400;
        color: #fff;
        background-color: #2fcf0d;
        border-radius: 27px;
        padding: 11px 30px 7px;
        display: block;
        text-align: center;
        text-decoration: none;
      }
    }
  }
}
